import Image from "next/image";

const Header = ({ mode, step, currentPage, totalPages, uploadProgress }) => {
    let progressPercentage = 0;
    let progressText = "";

    if (step === 2 && totalPages > 0) {
        progressPercentage = Math.round(((currentPage + 1) / totalPages) * 100);
        progressText = `${progressPercentage}% Done`;
    } else if (step === 3) {
        const resumeProgress = uploadProgress?.resume || 0;
        const coverLetterProgress = uploadProgress?.coverLetter || 0;
        progressPercentage = Math.max(resumeProgress, coverLetterProgress);
        progressText = `${progressPercentage}% Done`;
    }

    return (
        <header className="bg-[#fff8f7] border-[#231812] border-b shadow-lg text-white py-4 md:py-6 px-4 md:px-10 flex items-center">
            <div className="flex justify-around items-center w-full">
                <div className="flex-shrink-0">
                    <Image
                        src={mode === "dark" ? "/assets/images/logo-white.svg" : "/assets/images/logo.svg"}
                        alt="Logo"
                        width={240}
                        height={40}
                        className="w-32 md:w-[200px]"
                    />
                </div>
                {(step === 2 || step === 3) && (
                    <div className="w-1/2 relative">
                        <div className="w-full bg-gray-200 rounded-lg h-8">
                            <div
                                className="bg-[#f05d23] h-8 rounded-lg transition-all duration-300 flex items-center justify-center text-white text-base font-semibold"
                                style={{ width: `${progressPercentage}%` }}
                            >
                                {progressText}
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </header>
    );
};

export default Header;