// src/pages/interview.js
import { useState } from "react";
import { questions } from "@/data/questions";
import Header from "@/layouts/header";
import toast, { Toaster } from "react-hot-toast";
import { useFormData } from "@/hooks/useFormData";
import { useFileUpload } from "@/hooks/useFileUpload";
import Step1Form from "@/components/Step1Form";
import Step2Questions from "@/components/Step2Questions";
import Step3Documents from "@/components/Step3Documents";
import Step4Confirmation from "@/components/Step4Confirmation";

export default function InterviewPage() {
    const [step, setStep] = useState(1);
    const [currentPage, setCurrentPage] = useState(0);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const { formData, setFormData, submissionStatus, setSubmissionStatus, handleChange, handleOptionToggle, fileToBase64 } =
        useFormData();
    const fileUploadProps = useFileUpload(formData, setFormData);

    const questionsPerPage = 5;
    const totalPages = Math.ceil(questions.length / questionsPerPage);
    const currentQuestions = questions.slice(currentPage * questionsPerPage, (currentPage + 1) * questionsPerPage);

    const handleNext = async () => {
        if (step === 1) {
            if (!formData.fullName || !formData.email) {
                toast.error("Please fill in your name and email.");
                return;
            }
            setStep(2);
            toast.success("Great! Let’s move to the questions.");
        } else if (step === 2) {
            if (!isPageComplete()) {
                toast.error("Please answer all questions on this page.");
                return;
            }
            if (currentPage < totalPages - 1) {
                handleNextPage();
            } else {
                setStep(3);
                toast.success("All questions completed! Upload your documents next.");
            }
        } else if (step === 3) {
            setIsSubmitting(true);
            const maxFileSize = 5 * 1024 * 1024; // 5MB
            if (formData.resume && formData.resume.size > maxFileSize) {
                toast.error("Resume file size exceeds 5MB limit.");
                setIsSubmitting(false);
                return;
            }
            if (formData.coverLetter && formData.coverLetter.size > maxFileSize) {
                toast.error("Cover letter file size exceeds 5MB limit.");
                setIsSubmitting(false);
                return;
            }

            const dataToSend = {
                fullName: formData.fullName,
                email: formData.email,
                phone: formData.phone,
                linkedin: formData.linkedin,
                answers: formData.answers,
                resume: formData.resume ? await fileToBase64(formData.resume) : null,
                coverLetter: formData.coverLetter ? await fileToBase64(formData.coverLetter) : null,
            };

            console.log("Data to send:", {
                ...dataToSend,
                resume: dataToSend.resume ? "present" : "none",
                coverLetter: dataToSend.coverLetter ? "present" : "none",
            });

            const submitToast = toast.loading("Submitting your application...");

            const simulateProgress = () => {
                let progress = 0;
                const interval = setInterval(() => {
                    progress += 10;
                    fileUploadProps.setUploadProgress({
                        resume: formData.resume ? Math.min(progress, 100) : 0,
                        coverLetter: formData.coverLetter ? Math.min(progress, 100) : 0,
                    });
                    if (progress >= 100) clearInterval(interval);
                }, 200);
                return interval;
            };

            const progressInterval = simulateProgress();

            try {
                const response = await fetch("/api/submit", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json",
                    },
                    body: JSON.stringify(dataToSend),
                });
                const result = await response.json();
                if (response.ok) {
                    clearInterval(progressInterval);
                    fileUploadProps.setUploadProgress({ resume: 100, coverLetter: 100 });
                    toast.success("Submission successful!", { id: submitToast });
                    setSubmissionStatus({ success: true, score: result.score });
                    setStep(4);
                } else {
                    throw new Error(result.error || "Unknown error");
                }
            } catch (error) {
                clearInterval(progressInterval);
                toast.error(`Submission failed: ${error.message}`, { id: submitToast });
                setSubmissionStatus({ success: false, message: error.message });
            } finally {
                setIsSubmitting(false);
            }
        }
    };

    const handleBack = () => {
        if (step === 2 && currentPage > 0) {
            setCurrentPage(currentPage - 1);
            toast("Going back to the previous set...", { icon: "⬅️" });
        } else if (step > 1) {
            setStep(step - 1);
            toast("Returning to the previous step...", { icon: "⬅️" });
        }
    };

    const handleNextPage = () => {
        if (currentPage < totalPages - 1) {
            setCurrentPage(currentPage + 1);
            toast("Moving to the next set of questions...", { icon: "➡️" });
        }
    };

    const handleQuestionsComplete = () => {
        setStep(3);
        toast.success("All questions completed! Upload your documents next.");
    };

    const isPageComplete = () => currentQuestions.every((q) => formData.answers[q.id - 1]?.length > 0);

    return (
        <>
            <Header
                mode="light"
                step={step}
                currentPage={currentPage}
                totalPages={totalPages}
                uploadProgress={fileUploadProps.uploadProgress}
            />
            <div className="min-h-screen bg-gray-100 flex flex-col justify-center items-center">
                <div className="max-w-3xl w-full mx-auto p-6">
                    <Toaster position="top-right" reverseOrder={false} />

                    {step === 1 && <Step1Form formData={formData} handleChange={handleChange} />}
                    {step === 2 && (
                        <Step2Questions
                            formData={formData}
                            handleOptionToggle={handleOptionToggle}
                            currentPage={currentPage}
                            questionsPerPage={questionsPerPage}
                            questions={questions}
                            handleNextPage={handleNextPage}
                            totalPages={totalPages}
                            onComplete={handleQuestionsComplete} // Pass callback
                        />
                    )}
                    {step === 3 && (
                        <Step3Documents
                            formData={formData}
                            setFormData={setFormData}
                            isSubmitting={isSubmitting}
                            uploadProgress={fileUploadProps.uploadProgress}
                            setUploadProgress={fileUploadProps.setUploadProgress}
                            {...fileUploadProps}
                        />
                    )}
                    {step === 4 && <Step4Confirmation formData={formData} submissionStatus={submissionStatus} />}

                    {step !== 4 && (
                        <div className="mt-6 flex justify-between">
                            <button
                                onClick={handleBack}
                                disabled={step === 1 || isSubmitting}
                                className="px-4 py-2 bg-gray-400 text-white rounded disabled:opacity-50 hover:bg-gray-500 transition"
                            >
                                Back
                            </button>
                            <button
                                onClick={handleNext}
                                disabled={(step === 2 && !isPageComplete()) || isSubmitting}
                                className="w-full md:w-auto px-4 py-2 bg-[#f05d23] text-white rounded hover:bg-[#d94f1e] disabled:bg-gray-300 disabled:text-gray-600 transition"
                            >
                                {step === 3 ? "Submit" : "Next"}
                            </button>
                        </div>
                    )}
                </div>
            </div>
        </>
    );
}