// src/components/DragDropZone.js
export default function DragDropZone({
                                         type,
                                         file,
                                         isDragging,
                                         uploadProgress,
                                         isSubmitting,
                                         handleFileChange,
                                         handleDrop,
                                         handleDragOver,
                                         handleDragLeave,
                                         removeFile,
                                     }) {
    return (
        <div
            onDrop={(e) => handleDrop(e, type)}
            onDragOver={(e) => handleDragOver(e, type)}
            onDragLeave={(e) => handleDragLeave(e, type)}
            className={`p-6 flex justify-center bg-white border-2 border-dashed rounded-xl transition-all duration-300 ${
                isDragging ? "border-[#f05d23] bg-orange-50" : "border-gray-300"
            }`}
        >
            {!file ? (
                <div className="text-center">
                    <span className="inline-flex justify-center items-center w-10 h-10 bg-gray-100 text-gray-800 rounded-full">
                        <svg
                            className="w-6 h-6"
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                            strokeWidth="2"
                        >
                            <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
                            <polyline points="17 8 12 3 7 8" />
                            <line x1="12" x2="12" y1="3" y2="15" />
                        </svg>
                    </span>
                    <div className="mt-4 text-sm text-gray-600">
                        <span className="font-medium text-gray-800">Drop your {type} here or </span>
                        <input
                            type="file"
                            name={type}
                            accept=".pdf,.docx"
                            onChange={(e) => handleFileChange(e, type)}
                            className="hidden"
                            id={`${type}-upload`}
                        />
                        <label
                            htmlFor={`${type}-upload`}
                            className="font-semibold text-[#f05d23] hover:text-[#d94f1e] cursor-pointer transition"
                        >
                            browse
                        </label>
                    </div>
                    <p className="mt-1 text-xs text-gray-400">PDF or DOCX, max 5MB</p>
                </div>
            ) : (
                <div className="w-full p-3 bg-white border border-gray-300 rounded-xl">
                    <div className="mb-1 flex justify-between items-center">
                        <div className="flex items-center gap-x-3">
                            <span className="w-10 h-10 flex justify-center items-center border border-gray-200 text-gray-500 rounded-lg">
                                {file.name.endsWith(".pdf") ? (
                                    <svg
                                        className="w-5 h-5"
                                        xmlns="http://www.w3.org/2000/svg"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        stroke="currentColor"
                                    >
                                        <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8l-6-6z" />
                                        <path d="M14 2v6h6" />
                                    </svg>
                                ) : (
                                    <svg
                                        className="w-5 h-5"
                                        xmlns="http://www.w3.org/2000/svg"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        stroke="currentColor"
                                    >
                                        <path d="M7 21h10a2 2 0 0 0 2-2V5a2 2 0 0 0-2-2H7a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2z" />
                                    </svg>
                                )}
                            </span>
                            <div>
                                <p className="text-sm font-medium text-gray-800 truncate max-w-[200px]">
                                    {file.name}
                                </p>
                                <p className="text-xs text-gray-500">{(file.size / 1024).toFixed(2)} KB</p>
                            </div>
                        </div>
                        <button
                            onClick={() => removeFile(type)}
                            className="text-gray-500 hover:text-gray-800 transition"
                        >
                            <svg
                                className="w-4 h-4"
                                xmlns="http://www.w3.org/2000/svg"
                                fill="none"
                                viewBox="0 0 24 24"
                                stroke="currentColor"
                                strokeWidth="2"
                            >
                                <path d="M3 6h18" />
                                <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6" />
                                <path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2" />
                                <line x1="10" x2="10" y1="11" y2="17" />
                                <line x1="14" x2="14" y1="11" y2="17" />
                            </svg>
                        </button>
                    </div>
                    {isSubmitting && (
                        <div className="flex items-center gap-x-3">
                            <div className="w-full h-2 bg-gray-200 rounded-full overflow-hidden">
                                <div
                                    className="h-full bg-[#f05d23] rounded-full transition-all duration-500"
                                    style={{ width: `${uploadProgress[type]}%` }}
                                />
                            </div>
                            <div className="w-10 text-end">
                                <span className="text-sm text-gray-800">{uploadProgress[type]}%</span>
                            </div>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}